@import './animations';
@import './grid';
@import './mixins';
@import './typography';
@import './variables';
@import './utilities';


* {
    margin: 0;
    padding: 0;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    font-size: 62.5%; // 1rem = 10px, 10px/16px = 62.5%

}

body {
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    line-height: 1.6;
    color: $color-grey-dark-2;
    background-color: $color-grey-light-1;
    min-height: 100vh;
    margin-top: 7rem;
}